  mounted() {
    if (process.client) {
      let isFristMap;
      let key = setTimeout(() => {
        this.getMap();
        isFristMap = true;
      }, 1000);
      window.onload = () => {
        if (isFristMap) return;
        clearTimeout(key);
        this.getMap();
      };
    }
  },
  computed: {
    // 判断当前是否为移动端布局
    mobileStatus() {
      return this.$store.state.isMobile;
    },
    updateTime() {
      let time = new Date();

      let month =
        time.getMonth() + 1 < 10
          ? "0" + (time.getMonth() + 1)
          : time.getMonth() + 1;
      let day = time.getDate() < 10 ? "0" + time.getDate() : time.getDate();

      let str = time.getFullYear() + "." + month + "." + day;

      return str;
    }
  },
  methods: {
    handleHeight(id) {

      /* 由于在vue中不推荐控制dom, 即使我们是在查看属性也是, 确保vue成功定位到dom  并且返回数据 */
      let element = this.$refs["transform_box" + id];
      let height = element[0].clientHeight;

      return `height: ${height}px; transition: height ${height * 0.0012}s`;
    },
    handleHeightToM(id) {

      /* 由于在vue中不推荐控制dom, 即使我们是在查看属性也是, 确保vue成功定位到dom  并且返回数据 */
      let element = this.$refs["transform_box_m" + id];

      let height = element[0].clientHeight;

      return `height: ${height}px; transition: height ${height * 0.0012}s`;
    },
    getMap() {
      if (process.client) {
        // AMap是高德地图的构造函数，这里.Map是创建地图，.Marker是创建坐标点   28.2076936875,112.8821611404
        let mapObj = new AMap.Map("mapContainer", {
          //'container'是对应页面盒子的id
          resizeEnable: true, //自适应大小
          zoom: 17, //初始视窗
          center: [112.8821611404, 28.2076936875] //中心点
        });
        this.map = new AMap.Marker({
          map: mapObj,
          position: new AMap.LngLat("112.8821611404", "28.2076936875"), //此处根据页面数据可以直接传入经纬度进行描点
          icon: new AMap.Icon({
            image: require("~/assets/img/here.png"),
            size: new AMap.Size(178, 54),
            imageSize: new AMap.Size(178, 54)
          }),
          offset: new AMap.Pixel(-89, -54)
        });
        this.map.on("click", this.markOnAMAP);
      }
    },
    // 跳转到高德地图app上进行操作
    markOnAMAP() {

      this.map.markOnAMAP({
        position: this.map.getPosition(),
        name: "蜂乐购优选-湖南蜂乐购电子商务有限公司"
      });
    }
  },
  watch: {
    mobileStatus() {
      this.$nextTick(() => {
        this.getMap();
      });
    }
  }
